export default class SRResponse {

    constructor() {
        this.rpid = "";
        this.code = "";
        this.msg = "";
        this.result = "";
        this.requestInfo = null;
    }

    isSucess(){
        return this.code === "0000";
    }


    static create(response){
        var srResponse = new SRResponse();
        for(var key in response){
            srResponse[key] = response[key];
        }
        return srResponse;
    }
}


