export default class User  {


    constructor() {
        this.userId = "";
        this.name = "";
        this.password = "";
    }


    static create(json){
        var user = new User();
        user.userId = json["userId"];
        user.name = json["name"];
        user.password = json["password"];
        return user;
    }
}


