var _loginUser = null;

export default class UserManager  {


 
    static setUser(user){
        _loginUser = user;
        if(user){
            sessionStorage._loginUser = JSON.stringify(user);
        }else{
            sessionStorage._loginUser = null;
        } 
    }
    
    static getUser(){
        return _loginUser;
    }
    
    static isLogin(){
        return _loginUser != null;
    }


    //缓存恢复
    static _setUser(user){
        UserManager.setUser(user);
    }
}



