import React, {Component} from 'react';
import ReactDOM from 'react-dom'

 

import SRList from '../widget/SRList';
import SRListItem from '../widget/SRList';

import User from '../model/User.js';
import UserManager from '../model/UserManager.js';

import SRConnectInfo from '../net/SRConnectInfo.js';
import SRService from '../net/SRService.js';
import SRRequest from '../net/SRRequest.js';

class App {
    constructor() {
        this.name = "";
        this.icon = "";
        this.versionName = "";
        this.size = "";
        this.date = "";
        this.packageName = "";
    }

    static create(json) {
        var app = new App();
        app.name = json["appName"];
        app.packageName = json["packageName"];
        app.versionName = json["versionName"];
        return app;
    }
}

export default class AppManagerPage extends Component {

    constructor(props, context) {
        super(props, context);
        this.state = {
            apps :[]
        };
        console.log(UserManager.getUser());
    }


    render() {
        return (
            <div>

                <div  className="toolbar">
                    <div >
                        <button onClick={this.onRefreshClicker.bind(this)}>刷新</button>
                        <button onClick={this.onUninstallClicker.bind(this)}>卸载</button>
                        <button onClick={this.onDownloadClicker.bind(this)}>下载</button>
                        <button onClick={this.onInstallClicker.bind(this)}>安装</button>
                    </div>
                </div>

                <div>
                    <SRList ref="appList">
                        {this.getAppItem()}
                    </SRList>
                </div>
            </div>
        );
    }

    getAppItem() {
        var appList = this.state.apps;
        var listItems = appList.map((item, index)=> {
            return (
                <SRListItem key={index} app={item}>
                    <div className="app_layout" >
                        <input ref={"appCheckboxView-" + index} id="app_checkbox" type="checkbox"
                               onChange={this.onChengeClicker.bind(this)}/>
                        <img id="app_icon" src={require('../img/app.png')}/>
                        <div id="app_info">
                            <p>{item.name}</p>
                            <p>{item.versionName || "-"}</p>
                        </div>
                        <p id="app_sub_info">{item.size} {item.date}</p>
                    </div>
                </SRListItem>
            );

        });
        return listItems;
    }

    onChengeClicker(){
        console.log("onChengeClicker");
        console.log(this);
    }

    onRefreshClicker() {
        console.log("刷新");
        var request = {
            rpid : new Date().getTime() + "",
            method: "Cmd",
            params: {
                CMD:"Pm",
                "-listPackage": 0x00000001 + "",
                "-flag_system": "0"
            }
        };
        var serRequest = SRRequest.create(request);

        var callback = {
            doSucess:(response)=>{
                console.log("callback doSucess==> ");
                console.log(response);
                if(response.isSucess()){
                    var apps = [];
                    response.result.forEach((value, key, map)=> {
                        var app = App.create(value);
                        apps.push(app);
                    });
                    this.setState({
                        apps :apps
                    });
                }
                //
            },
            doFailed:(response)=>{
                console.log("callbac doFailedk==> ");
                console.log(response);
            }
        };
        SRService.requestInfo(serRequest, callback);
    }

    /**
     * 下载app
     */
    onDownloadClicker() {
        alert("onDownloadClicker");

    }

    /**
     * 卸载app
     */
    onUninstallClicker() {
        //alert("onUninstallClicker");
        console.log("onUninstallClicker");
        var appListView = this.refs.appList;

        var unApps = [];
        React.Children.forEach(appListView.props.children, (child) => {
            var isChecked = this.refs[`appCheckboxView-${child.key}`].checked;
            if(isChecked){
                unApps.push({
                    packageName: child.props.app.packageName
                })
            }

        }); 
        var request = {
            rpid : new Date().getTime() + "",
            method: "Cmd",
            params: {
                CMD:"Pm",
                "-unInstall": unApps[0].packageName
            }
        };
        var serRequest = SRRequest.create(request);

        var callback = {
            doSucess:(response)=>{
                console.log("callback doSucess==> ");
                console.log(response);

            },
            doFailed:(response)=>{
                console.log("callbac doFailedk==> ");
                console.log(response);
            }
        };
        SRService.requestInfo(serRequest, callback);


    }

    onInstallClicker() {
        console.log("onInstallClicker");
        var refNode = "message";
        wilddog.sync().ref(refNode).on('child_added', function (snapshot) {
            console.log("SRService 获取到key:" + snapshot.key());
            var text = snapshot.val();
            console.log(text);

        });
    }
}




