/**
 * In this file, we create a React component
 * which incorporates components provided by Material-UI.
 */
import React, {Component} from 'react';
import {Router, Route, Link,browserHistory, hashHistory, History} from 'react-router';

import AppManagerPage from './page/AppManagerPage.js';
import LoginPage from './page/LoginPage.js';

import User from './model/User.js';
import UserManager from './model/UserManager.js';
import SRConnectInfo from './net/SRConnectInfo.js';
import SRService from './net/SRService.js';

import less from './app.less';


window.onload = function () {
    console.log("重新加载....");
    SRService.init();
    if(sessionStorage._loginUser != null){
        console.log("session恢复用户信息");
        console.log(sessionStorage._loginUser);
        var user = User.create(sessionStorage._loginUser);
        UserManager._setUser(user);
    }

    if(sessionStorage._connectInfo  != null){
        console.log("session恢复网络信息");
        console.log(sessionStorage._connectInfo);
        var connectInfo = JSON.parse(sessionStorage._connectInfo);
        // SRService._setConnectInfo(connectInfo);
        // //初始化

        SRService.reset(connectInfo);
    }



};

class Main extends Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            open: false,
        };
    }


    render() {
        return (
            <LoginPage >
            </LoginPage>
        );
    }
}

const styles = {
    container: {
        textAlign: 'center',
        paddingTop: 200,
    },
};



var Routers = (
    <Router history={hashHistory}>
        <Route path="/" component={Main}>

        </Route>
        <Route path="/AppManagerPage" component={AppManagerPage} />

    </Router>
);
export default Routers;


