export default class SRRequest {

    constructor() {
        this.rpid = "";
        this.method = "";
        this.params = new Map();
    }


    getJson(){
        var json ={
            rpid : this.rpid,
            method : this.method,
            params : this.params
        }
        return JSON.stringify(json);
    }

    static create(request){
        var srReqeust = new SRRequest();
        for(var key in request){
            srReqeust[key] = request[key];
        }
        return srReqeust;
    }
}


