import React from 'react';

import User from '../model/User.js';
import UserManager from '../model/UserManager.js';

import SRConnectInfo from '../net/SRConnectInfo.js';
import SRService from '../net/SRService.js';
import SRRequest from '../net/SRRequest.js';

/**
 * 登录页面
 */
export default class LoginPage extends React.Component {

    constructor(props, context) {
        super(props, context);
        this.state = {
            name: "",
            password: ""
        };
    }


    render() {
        return (
            <div className="login_container">
                 <div className="login_content">
                     <div className="login_layout">
                         <p>用户名:</p>
                         <input
                             onChange={(event)=>{
                                this.setState({name:event.target.value})
                             }}
                             value={this.state.name}/>
                     </div>
                     <div className="login_layout">
                         <p>密&nbsp;&nbsp;&nbsp;&nbsp;码:</p>
                         <input
                             type="password"
                             onChange={(event)=>{
                                this.setState({password:event.target.value})
                             }}
                             value={this.state.password}/>
                     </div>
                     <button
                         onClick={this.onSubmitClicker.bind(this)}
                         className="login_submit">
                         登录
                     </button>
                 </div>
            </div>
        );
    }

    onSubmitClicker(event){
        if(!this.state.name || !this.state.password){
            alert("用户名或密码为空");
            //event.preventDefault();
            return;
        }


        var user = User.create({
                name: this.state.name,
                password : this.state.password
            }
        );

        //this.onInstallClicker();

        var request = {
            rpid : new Date().getTime() + "",
            method: "Login",
            params: {
                name : user.name,
                password : user.password
            }
        };
        var callback = {
            doSucess:(response)=>{
                if(response.isSucess()){
                    user.userId = response.result.userId;
                    UserManager.setUser(user);
                    //初始化连接信息
                    var conn = new SRConnectInfo();
                    conn.user = user.userId;
                    SRService.reset(conn);
                    //页面跳转
                    var path = '/AppManagerPage';
                    this.context.router.push(path);
                }else{
                    alert(response.msg);
                }
            },
            doFailed:(response)=>{
                alert(response.msg);
            }
        };
        SRService.requestInfo(request, callback);
    }
}

LoginPage.contextTypes = {
    router: React.PropTypes.object
};




