import  wilddog from 'wilddog';
import SRRequest from './SRRequest.js';
import SRResponse from './SRResponse.js';


//网络超时1分钟
var Net_TimeOut = 1000 * 60;

/**
 * 回调,
 * 成功,如果response不为空则回调
 * 失败,response为空并超时
 * key    rpid
 * value  callback
 *        dateTime
 *        request   (SRRequest请求信息)
 *        response
 */
var _callbackMap = new Map();
/***
 * 计时器,秒计算
 */
var _timeCount = 0;
/**连接信息*/
var _connectInfo = null;

var _time = null;

export default class SRService {


    //缓存恢复
    static _setConnectInfo(connectInfo){
        _connectInfo = connectInfo;
    }


    /**
     *  userName
     * @param connectInfo
     */
    static  reset(connectInfo) {
        //user
        _connectInfo = connectInfo;
        if(connectInfo){
            sessionStorage._connectInfo= JSON.stringify(connectInfo);
        }else {
            sessionStorage._connectInfo = null;
        }

        if (_connectInfo == null) {
            console.error("SRService 初始化失败: 连接信息为空");
            return false;
        }

        if (_connectInfo["user"] == null) {
            console.error("SRService 初始化失败: 用户信息为空");
            return false;
        }


        console.log("==================初始化net服务==================");
        console.log(connectInfo);

        var refNode = "message/" + _connectInfo["user"] + "/response";
        console.log("SRService response refNode:" + refNode);
        wilddog.sync().ref(refNode).remove();
        wilddog.sync().ref(refNode).on('child_added', function (snapshot) {
            console.log("SRService 获取到key:" + snapshot.key());
            var text = snapshot.val();
            console.log(text);
            var srResponse = SRResponse.create(JSON.parse(text));
            if (srResponse && srResponse["rpid"]) {
                var callbackInfo = _callbackMap.get(srResponse.rpid);
                if (callbackInfo) {
                    callbackInfo.response = srResponse;// 下一秒回调
                }
            }
        });

        //回调
        SRService._stopTime();
        SRService._startTime(()=> {
            //计数器,加1
            _timeCount++;
            //console.log("SRService _timeCount:" + _timeCount);
            //
            _callbackMap.forEach((value, key, map)=> {
                if (value.response) {
                    console.log("SRService 回调");
                    console.log(value);
                    //返回内容, 移除回调信息进行回调
                    map.delete(key);
                    value.callback.doSucess(value.response);
                } else {
                    //超时回调
                    var diffTime = (_timeCount - value.dateTime) * 1000;
                    if (diffTime > Net_TimeOut) {
                        console.log("SRService 超时回调");
                        console.log(value);
                        map.delete(key);
                        var response = {
                            requestInfo: value.request,
                            code: "-1",
                            msg: "超时",
                            rpid: value.request.rpid
                        };
                        var srResponse = SRResponse.create(response);
                        value.callback.doFailed(srResponse);
                    }
                }
            });
        });
    }

    static _stopTime() {
        _time && clearInterval(_time);
    }

    static _startTime(timeCallback) {
        _time = setInterval(()=> {
            if (timeCallback) {
                timeCallback();
            }
        }, 1000);
    }

    /**
     *
     *  var request = {
            rpid : new Date().getTime(),
            method: "Cmd",
            params: {
                CMD:"Pm",
                "-listPackage": ""
            }
        };
     * @param request
     * @param callback
     */
    static requestInfo(request, callback) {
        if(request.method == "Login"){
            SRService.login(request, callback);
            return;
        }

        if (request == null || request["rpid"] == null) {
            console.error("SRService 发送失败: 请求信息有误");
            if (callback) {
                var response = {
                    requestInfo: request,
                    code: "-1",
                    msg: "请求信息有误",
                    rpid: request ? request.rpid : ""
                };
                var srResponse = SRResponse.create(response);
                callback(srResponse);
            }
            return;
        }
        var conn = _connectInfo;
        if (conn == null || conn["user"] == null) {
            console.error("SRService 发送失败: 未连接到服务");
            if (callback) {
                var response = {
                    requestInfo: request,
                    code: "-1",
                    msg: "命令发送失败",
                    rpid: request.rpid
                };
                var srResponse = SRResponse.create(response);
                callback(srResponse);
            }
            return;
        }

        //
        if (callback) {
            var value = {
                dateTime: _timeCount,
                callback: callback,
                request: request,
                response: null
            };
            _callbackMap.set(request.rpid, value);
        }

        var json = request.getJson();
        var refNode = "message/" + conn["user"] + "/request";
        console.log("request refNode:" + refNode);
        console.log(json);
        wilddog.sync().ref(refNode).push(json)
            .then(function (newRef) {
                // newRef 的地址类似下面：
                // https://<appId>.wilddogio.com/city/-JmRhjbYk73IFRZ7
                console.log("SRService ===================命令发送成功======================");
                console.log(newRef.toString());
                console.log("SRService 命令以发送");
            })
            .catch(function (err) {
                console.log("SRService ===================命令发送失败======================");
                console.log(err);
                var callbackInfo = _callbackMap.get(request.rpid);
                if (callbackInfo) {
                    var response = {
                        requestInfo: request,
                        code: "-1",
                        msg: "命令发送失败",
                        rpid: request.rpid
                    };
                    var srResponse = SRResponse.create(response);
                    callbackInfo.response = srResponse;// 下一秒回调
                }
            });
    }

    static login(request, callback){
        console.log("请求登录"); 
        wilddog.auth().signInWithEmailAndPassword(request.params.name, request.params.password)
            .then(function(res){
            console.log(res);
            if(callback){
                var response = {
                    requestInfo: request,
                    code: "0000",
                    msg: "成功",
                    rpid: request.rpid,
                    result:{
                        userId : res["uid"]
                    }
                };
                var srResponse = SRResponse.create(response);
                callback.doSucess(srResponse);
            }
        }).catch(function (error) {
            // 错误处理
            console.log(error);
            if(callback){
                var response = {
                    requestInfo: request,
                    code: "-1",
                    msg: error.message,
                    rpid: request.rpid,
                };
                var srResponse = SRResponse.create(response);
                callback.doFailed(srResponse);
            }
        });
    }
    
    static init(){
        console.log("初始化野狗服务");
        var config = {
            syncURL: "https://shenru.wilddogio.com",
            authDomain: "shenru.wilddog.com"//输入节点 URL
        };
        wilddog.initializeApp(config);
    }
}


